import { useState, useEffect } from "react";

const useFetch = (url) => {
  const [todos, setTodos] = useState([]);

  useEffect(() => {
    //           
    //fetch   ,      
    const abortCont = new AbortController();

    fetch(url)
      .then((res) => {
        if (!res.ok) {
          throw Error("could not fetch the data for that resource");
        }
        return res.json();
      })
      .then((data) => {
        setTodos(data);
      })
      .catch((err) => {
        console.log(err);
      });
    // abort the fetch.   DOM  
    return () => abortCont.abort();
  }, []);

  return [todos];
};

export default useFetch;
